import React, { Suspense, useRef, useEffect, lazy } from "react";
import { PostTrimContent, themeImpressionTracking } from '../../../../utils'
import { isTagShop } from "../../../../actions/api";
const Media = lazy(() => import('./Media'));
const AuthorDetails = lazy(() => import('./AuthorDetails'));
const ShopButton = lazy(() => import('../../Elements/ShopButton/ShopButton'));
const Network = lazy(() => import('../../Elements/Network/Network'));
const PostContent = lazy(() => import('../../Elements/PostContent/PostContent'));

  const trim_content_length = 200;
  const Card = ({ CardStyle, clickToShowPopUp, index, ThemeStyle, Post,  postSizeInPx,  onClickPopUpSlider }) => {

  const postTrack = useRef(null);
  const mediaType = Post?.type !== 1 ? true : false;
  const postTextContent = CardStyle?.content.trimcontent ? PostTrimContent(Post?.content?.text, trim_content_length) : Post?.content?.text
  const responsiveCardSize2x = postSizeInPx?.adjustWidth < 250 ? true : false
  const smallCardSize2x =  postSizeInPx.adjustWidth < 280 ? true : false
  const themeMode = 'dark'

  useEffect(() => {
    if (isTagShop) themeImpressionTracking(postTrack);
  }, []);
  useEffect(() => {
    if (Post?.isPopUp) onClickPopUpSlider(index, Post)
  }, [index, Post?.isPopUp, Post]);
  return (
    Post &&  
    <div id={`tb-gt-post-${Post.id}`} className={`tb_gt_post_wrapper${responsiveCardSize2x || smallCardSize2x ? ' tb_gt_post_wrapper2x' : ''}`} tb-network={Post.networkId} tb-product={ CardStyle.shopText != '' &&  Post.isShopIcon ? 1 : Post.isHotspot ? 2 : 0} ref={postTrack} post-id={Post.referenceId ? Post.referenceId : Post.id} feed-id={Post.feedId}>
      <div className={`tb_gt_post_in${isTagShop ? ' tb_gt_post_ani' : ''}`} onClick={clickToShowPopUp(index, Post)} role="article" aria-label={postTextContent} tabIndex={0}>
          {mediaType ? <Suspense fallback={<></>}><Media Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} index={index} /></Suspense> : ''}
          {!Post.isHotspot ? <>
            <div className="tb_gt_post_details" aria-hidden="true">
              {isTagShop ? <div className="tb_gt_social_" role="presentation">  
                <Suspense fallback={<></>}><Network networkClass={'tb_gt_social__ico'} networkId={Post.networkId} iconStyle={CardStyle.icon} /></Suspense>
              </div> : null}
              { Post.isShopIcon && CardStyle.shopText != '' ? <Suspense fallback={<></>}><ShopButton ThemeStyle={ThemeStyle} CardStyle={CardStyle} mode={1} btnClass={'tb_gt_btn_wrap'} /></Suspense> : null}
              {!isTagShop ? <>
                <div className="tb_gt_contant_wrapper">
                  {!CardStyle.content.status || Post.type == 1 ? <Suspense fallback={<></>}>
                  <PostContent
                      contentClass={'tb_gt_content'}
                      item={Post}
                      content={postTextContent}
                      CardStyle={CardStyle}
                      contentTitle={Post.contentTitle}
                    /> 
                    </Suspense> : ''}
                </div>
                <Suspense fallback={<></>}>
                  <AuthorDetails Post={Post}  CardStyle={CardStyle} />
                </Suspense>
              </>
                : null}
            </div>
            <div className={`tb_gt_post_overlay tb_gt_post_overlay-${themeMode}`}><div></div></div>
          </> : null}
        </div>
    </div>
  );
}
export default Card